"use client"

import { useEffect, useState, useRef } from "react";
import { Lot } from "../page";

type NotifyProps = {
    notifyLots: Lot[];
    onAddLot: (lot: Lot) => void;
    onNewLot: (lot: Lot[]) => void;
}

export default function Notify({
    onAddLot,
    onNewLot,
    notifyLots
}: NotifyProps) {

const [lastSync, setLastSync] = useState<number>(Date.now());

const [hoverId, setHoveredId] = useState<string | null>(null);



useEffect(() => {
    const fetchLots = async () => {
        try {
            const res = await fetch(`/api/lots?since=${lastSync}`);
            const serverLots = await res.json()
            if(serverLots.length) onNewLot(serverLots)
            setLastSync(Date.now())
    
        } catch(err){
            console.error(err);
        }
    }

    const interval = setInterval(fetchLots, 5000);
    return () => clearInterval(interval);
    
}, [lastSync])



    return (
        <div className="flex flex-col w-full border-l">
            {notifyLots.map(item => (
                <div key={item.id}
                    onMouseEnter={()=> setHoveredId(item.id)}
                    onMouseLeave={()=> setHoveredId(null)}
                    className={`flex gap-1 w-full border p-2 cursor-grab`}
                    draggable
                    onDragStart={(e) => {
                        e.dataTransfer.setData(
                            "application/lot",
                            JSON.stringify(item)
                        )
                    }}
                    
                >
                    <div className="">
                        {item.label} - {item.weight}—Ä
                    </div>


                    <div className={`flex gap-2 
                                   ${hoverId === item.id ? "" : "hidden"} 
                        `}>
                        <button onClick={()=> {
                            onAddLot(item);
                        }}>
                            +
                        </button>
                        <button onClick={()=> {
                        }}>
                            -
                        </button>
                    </div>
                </div>
            ))}
        </div>
    )
}