"use client"

import { useEffect, useState, useRef } from "react";

import { generateColor, polarToCartesian, describeArc, getTextCoords } from "./utils/sectorMath";

import Notify from "./notification/page";

export type Lot = {
    id: string;
    label: string;
    weight: number;
    color?: string;
    enabled?: boolean;
    createdAt: number;
}


export default function Roll() {
    
/* ------------------ STATE ------------------ */
const [lots, setLots] = useState<Lot[]>([])
const [notifyLots, setNotifyLots] = useState<Lot[]>([])

const [hydrated, setHydrated] = useState(false);


const [rotation, setRotation] = useState(0);
const [result, setResult] = useState<Lot | null>(null);

const [pending, setPending] = useState<Lot | null>(null); //–ª–æ—Ç –≤—ã–±—Ä–∞–Ω –Ω–æ –µ—â–µ –∫—Ä—É—Ç–∏—Ç—Å—è –∫–æ–ª–µ—Å–æ 
const wheelRef = useRef<HTMLDivElement | null>(null);  // ref –Ω–∞ –∫–æ–ª–µ—Å–æ

const [lotName, setLotName] = useState('');
const [lotCost, setLotCost] = useState('');

const [selectedinterval, setSelectedInterval] = useState(5); // –≤—Ä–µ–º—è –≤—Ä–∞—â–µ–Ω–∏—è –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é

const [hoveredLotId, setHoveredLotId] = useState<string | null>(null); //–≤–∏–∑—É–∞–ª—å–Ω—ã–∏ÃÜ —ç—Ñ—Ñ–µ–∫—Ç –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ –Ω–∞ –ª–æ—Ç

useEffect(() => {
    try{
        const saved = localStorage.getItem("lots")
        if (saved) setLots(JSON.parse(saved))
    } catch {} 
    setHydrated(true);
}, []);

useEffect(() => {
    if (hydrated) {
        localStorage.setItem("lots", JSON.stringify(lots))
    }
}, [lots, hydrated]);


useEffect(() => {
    const wheel = wheelRef.current;
    if (!wheel) return;

    const handleTranstionEnd = (e: TransitionEvent) => {
        if (e.propertyName !== 'transform') return;
        if(!pending) return;

        setResult(pending);

        setLots(prev => prev.map(lot => lot.id === pending.id ? {...lot, enabled: false} : lot));
        setPending(null);

    }
    wheel.addEventListener('transitionend', handleTranstionEnd);

    return () => {
        wheel.removeEventListener('transitionend', handleTranstionEnd);
    }
}, [pending])





if (!hydrated) return null;

// –†–∞–∑–º–µ—Ä—ã –∫–æ–ª–µ—Å–∞
const size = [500, 500, 490]
const cx = size[0] / 2;
const cy = size[1] / 2;
const radius = size[2] / 2;

const activeLots = lots
    .filter(lot => lot.enabled !== false)
    .map(l => {
        return {
            ...l,
            weight: 1 / l.weight
        }
    });

const disabledLots = lots.filter(lot => lot.enabled === false);
const filteredLots = lots.sort((lot1, lot2) => lot2.weight - lot1.weight); //—Ñ–∏–ª—å—Ç—Ä –¥–ª—è –≤—ã–≤–æ–¥–∞ –ª–æ—Ç–æ–≤

const totalWeight = activeLots.reduce((sum, lot) => sum + lot.weight, 0); //—Å—É–º–º–∞ –≤–µ—Å–æ–≤
const angelPerWeight = 360 / totalWeight; // —É–≥–æ–ª –∫–∞–∂–¥–æ–≥–æ –ª–æ—Ç–∞





function pickLot(activeLots: Lot[]) {
    let rand = Math.random() * totalWeight;

    for (const lot of activeLots) {
        rand -= lot.weight;
        if( rand <= 0 ){
            return lot;
        }
    }
    return activeLots[0];
}


    let currentAngle = 0;

    const sectors = activeLots.map((lot, index) => {
        const start = currentAngle;
        const angel = lot.weight * angelPerWeight;
        const end = start + angel;
        currentAngle = end;

        const coords = getTextCoords(cx,cy, radius, start, end);

        return {
            ...lot,
            start,
            end,
            path: describeArc(cx, cy, radius, start, end),
            textX: coords.x,
            textY: coords.y,
            textAngle: coords.rotation,
            color: generateColor(index),
        }
    })

    const spin = () => {
        const winner = pickLot(activeLots);

        setPending(winner);

        const winnerSector = sectors.find(s => s.id === winner.id)!;
        const middleAngle = (winnerSector.start + winnerSector.end) / 2;

        const spins = 5 * 360; // –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –ø–æ–ª–Ω—ã—Ö –æ–±–æ—Ä–æ—Ç–æ–≤
        const finalRotation = spins + (360 - middleAngle);

        setRotation(prev => prev + finalRotation);

    }

    
    const addLot = () => {
        const name = lotName || `–õ–æ—Ç ${lots.length + 1}`;
        const weight = Number(lotCost) || 0;

        if (!weight || weight <= 0 ) return;

        setLots(prev => [...prev, 
            { 
                id: crypto.randomUUID(), 
                label: name, 
                weight,
                color: generateColor(prev.length),
                enabled: true,
                createdAt: Date.now(),
            }
        ]);
        setLotName('');
        setLotCost('');
    }
    
    const removeLot = (id: string) => {
        setLots(prev => prev.filter(lot => lot.id !== id));
    }

    const chooseTime = (time: number) => {
        // —Ñ—É–Ω–∫—Ü–∏—è –¥–ª—è –≤—ã–±–æ—Ä–∞ –≤—Ä–µ–º–µ–Ω–∏ –≤—Ä–∞—â–µ–Ω–∏—è
        setSelectedInterval(time);
    }

    const returnLot = (id: string) => {
        setLots(prev => prev.map(lot => lot.id === id ? {...lot, enabled: true} : lot));
    }


    // –î–æ–±–∞–≤–ª—è–µ–º –ª–æ—Ç –∏–∑ notify
    const addLotsFromNotify = (newLots: Lot[]) => {
        setNotifyLots(prev => {
            const merged = [...prev];
            newLots.forEach(lot => {
                if(!merged.find(l => l.id === lot.id)) merged.push(lot)
            })
            return merged
        })
    }
    const addLotToMain = (lot: Lot) => {
        setLots(prev => [...prev, lot]);
        setNotifyLots(prev => prev.filter(l => l.id !== lot.id));
    }

    

    return (
        <div className="flex items-start justify-center">
            
            <div className="flex flex-col items-center gap-6 p-10">

            {/* –°—Ç—Ä–µ–ª–∫–∞ */}
            <div className="relative">
                <div className="absolute -top-4 left-1/2 -translate-x-1/2 z-10">
                üîª
                </div>

                {/* –ö–æ–ª–µ—Å–æ */}
                <div
                ref={wheelRef}
                style={{
                    transform: `rotate(${rotation}deg)`,
                    transition: `transform ${selectedinterval}s cubic-bezier(.17,.67,.12,.99)`,
                }}
                >
                <svg width={size[0]} height={size[1]}>

                    {activeLots.length === 1 && (
                        <>
                            <circle
                                cx={cx}
                                cy={cy}
                                r={radius}
                                fill={activeLots[0].color}
                                stroke="#fff"
                                strokeWidth={2}
                            />

                            <text
                                x={cx}
                                y={cy}
                                fill="#fff"
                                fontSize={12}
                                textAnchor="middle"
                                dominantBaseline="middle"
                            > 
                                {activeLots[0].label}
                            </text>
                        </>
                    )}

                    {activeLots.length > 1 &&
                        sectors.map((lot) => (
                            <g key={lot.id} 
                                onMouseEnter={() => setHoveredLotId(lot.id)}
                                onMouseLeave={() => setHoveredLotId(null)}
                            >
                                <path
                                    d={lot.path}
                                    fill={lot.color}
                                    stroke="#fff"
                                    strokeWidth={1}
                                    opacity={hoveredLotId === lot.id ? 0.5 : 1}
                                >
                                    
                                </path>

                                <text
                                    x={lot.textX}
                                    y={lot.textY}
                                    fill="#fff"
                                    fontSize="12"
                                    textAnchor="middle"
                                    dominantBaseline="middle"
                                    transform={`rotate(${lot.textAngle} ${lot.textX} ${lot.textY})`}
                                >
                                    {lot.label}
                                </text>
                            </g>
                        ))
                    }
                    
                </svg>
                </div>
            </div>

            {/* –ö–Ω–æ–ø–∫–∞ */}
            <button
                onClick={spin}
                className="px-6 py-2 bg-black text-white rounded-md"
            >
                –ö—Ä—É—Ç–∏—Ç—å
            </button>


            {/* –†–µ–∑—É–ª—å—Ç–∞—Ç */}
            {result && (
                <div className="text-lg font-semibold">
                –í—ã–ø–∞–ª–æ: {result.label}
                </div>
            )}
            </div>

            {/* –ö–æ–ª–æ–Ω–∫–∞ —Å–ø—Ä–∞–≤–∞ */}
            <div className="flex flex-col p-2 gap-4 border-l ">
                {/* –≤—Ä–∞—â–µ–Ω–∏–µ */}
                <div className="flex gap-2">
                    {[1,5,10,15].map((time) => (
                        <button
                        key={time}
                        onClick={() => chooseTime(time)}
                        className={`flex justify-center border rounded-md p-2 w-10 
                            ${selectedinterval === time ? 'bg-blue-500 text-white' : 'bg-white text-black'}
                            hover:bg-blue-300 transition-colors
                            `}
                        >
                        {time}
                        </button>
                    ))}
                </div>

                {/* –∏–Ω–ø—É—Ç—ã */}
                <div className="">
                    <form
                        onSubmit={(e) => {
                            e.preventDefault();
                            addLot();
                        }}
                    >
                        <input
                        value={lotName}
                        onChange={e => setLotName(e.target.value)}
                        placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ –ª–æ—Ç–∞"
                        className="border rounded-md p-2 mb-2 w-full"
                        >
                        </input>
                        <input
                        value={lotCost}
                        onChange={e => setLotCost(e.target.value)}
                        placeholder="–í–µ—Å –ª–æ—Ç–∞"
                        className="border rounded-md p-2 mb-2 w-full"
                        >
                        </input>

                        <button 
                        type="submit"
                        className="border rounded-md p-2">
                            –î–æ–±–∞–≤–∏—Ç—å –ª–æ—Ç
                        </button>
                    </form>
                </div>


                <div 
                    className="flex flex-col overflow-y-scroll max-h-100"
                >
                    <div>–õ–æ—Ç—ã</div>
                    {filteredLots.map((lot) => (
                        <div key={lot.id} 
                            className={`flex border rounded-md p-2 gap-2 hover:translate-x-0.5
                            ${hoveredLotId === lot.id ? 'bg-white/20' : ''} 
                            ${!lot.enabled ? 'opacity-50' : ''} 
                            `}
                            onMouseEnter={() => setHoveredLotId(lot.id)}
                            onMouseLeave={() => setHoveredLotId(null)}
                            onDragOver={(e)=> {
                                e.preventDefault();

                            }}
                            onDrop={(e) => {
                                const raw = e.dataTransfer.getData("application/lot")
                                if (!raw) return
                                const draggedLot = JSON.parse(raw) as Lot

                                setLots(prev => prev.map(l => l.id === lot.id 
                                    ? {...l, weight: l.weight + draggedLot.weight}
                                    : l
                                ))
                                
                                setNotifyLots(prev => prev.filter(l => l.id !== draggedLot.id))
                    }}
                        >

                            {lot.enabled ? <></> : <button onClick={() => returnLot(lot.id)}>+</button>}

                            <div className="flex-1">
                            {lot.label}
                            </div>

                            <input 
                            value={lot.weight || 1} 
                            onChange={e => {
                                const newWeight = Number(e.target.value);
                                setLots(prev=> prev.map(l=> l.id === lot.id ? {...l, weight: newWeight} : l))
                            }}
                            className="border rounded-md" />

                            <button onClick={() => removeLot(lot.id)}>X</button>
                        </div>
                    ))}
                </div>

            </div>

            <Notify 
            notifyLots={notifyLots}
            onAddLot={addLotToMain}
            onNewLot={addLotsFromNotify}
            >
            </Notify>

        </div>

    )
}