


export function polarToCartesian(cx: number, cy: number, r: number, angle: number){
    const rad = (angle - 90) * Math.PI / 180.
    return {
        x: cx + (r * Math.cos(rad)),
        y: cy + (r * Math.sin(rad)),
    }
}

export function describeArc(cx: number, cy: number, r: number, startAngle: number, endAngle: number) {

    const start = polarToCartesian(cx, cy, r, endAngle);
    const end = polarToCartesian(cx, cy, r, startAngle);
    const largeArcFlag = endAngle - startAngle > 180 ? 1 : 0;

    return `
        M ${cx} ${cy}
        L ${start.x} ${start.y}
        A ${r} ${r} 0 ${largeArcFlag} 0 ${end.x} ${end.y}
        Z
    `;
}


// –ü–æ–∑–∏—Ü–∏—è —Ç–µ–∫—Å—Ç–∞ –≤–Ω—É—Ç—Ä–∏ —Å–µ–∫—Ç–æ—Ä–∞
export function getTextCoords(cx: number, cy: number, r: number, startAngle: number, endAngle: number, factor: number = 0.65) {
    const angle = (startAngle + endAngle) / 2;

    const pos = polarToCartesian(cx, cy, r*factor, angle);

    let rotation = angle - 90;
    return { x: pos.x, y: pos.y, rotation };
}

export function generateColor(index: number) {
    const hue = (index * 137.508) % 360; // use golden angle approximation
    return `hsl(${hue}, 70%, 60%)`;
}